@import "https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: poppins, sans-serif;
}

body {
  min-height: 100vh;
  background-image: url("https://img.goodfon.ru/original/1920x1080/4/a7/cherry-blossom-tree-on-cliff-digital-wallpaper-cherry-blosso.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
  display: flex;
}

section {
  min-width: 600px;
  -webkit-backdrop-filter: blur(55px);
  backdrop-filter: blur(55px);
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid rgba(255, 255, 255, .5);
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  padding: 2rem 3rem;
  display: flex;
  position: relative;
}

.form {
  width: 100%;
}

h1 {
  color: #fff;
  text-align: center;
  font-size: 2rem;
}

.inputbox {
  max-width: 500px;
  border-bottom: 2px solid #fff;
  margin: 30px 0;
  position: relative;
}

.icon {
  fill: #fff;
  position: absolute;
  bottom: 5px;
  right: 2px;
}

.inputbox label {
  color: #fff;
  pointer-events: none;
  font-size: 1rem;
  transition: all .5s ease-in-out;
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
}

input:focus ~ label, input:valid ~ label {
  top: -5px;
}

.inputbox input {
  width: 100%;
  height: 60px;
  color: #fff;
  background: none;
  border: none;
  outline: none;
  padding: 0 35px 0 5px;
  font-size: 1rem;
}

.inputbox ion-icon {
  color: #fff;
  font-size: 1.2rem;
  position: absolute;
  top: 20px;
  right: 8px;
}

.button-wrapper {
  gap: 10px;
  display: flex;
}

button {
  width: 50%;
  height: 40px;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 40px;
  outline: none;
  font-size: 1rem;
  font-weight: 600;
  transition: all .4s;
}

button:hover {
  background-color: rgba(255, 255, 255, .5);
}

input[type="number"]::-webkit-outer-spin-button, input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

